import React from 'react';
//import GoogleMaps from "../components/map";
import GoogleMaps from '../components/maps';
import Punchline from '../components/punchline';
import Message from '../components/disqus/message';//'disqus-react' // react-disqus-thread
import { Badge, Card } from 'reactstrap';

class BlogPage extends React.Component {

	render(){
		function handleNewComment(props) {
		}
	    return (
	    	<div className="container backpage-main-content">
	    	<Punchline tag="Madhav Shandilya:- Portfolio Synopsis, Hightech Apps Demo & Micro-Blogging Platform"/>
				<GoogleMaps key="map"/>
				<div className="pane">
					<h4 className="pane-title">H111EY - Appreciate if you can provide the valuable feedback on portfolio to make it better!</h4>
				</div>
				<div className="pane">
					<h4 className="pane-title">HE444Y - Appreciate if you can provide the valuable feedback on portfolio to make it better!</h4>
				</div>
				<Disqus shortname="sysforge-disqus-com"
					identifier="sysforge-disqus-com"
					title="SYSFORGE"
					url="https://sysforge-disqus-com.disqus.com"
					category_id="123456"
					onNewComment={this.handleNewComment}/>


			</div>
	    )
	}
}

const BlogPagefn = () => (
	<div className="animated fadeIn">
		<GoogleMaps key="map"/>
		<h1><Badge color="primary">Feedback Appreciated!</Badge></h1>
		<Card body>
		  <Message/>
	  </Card>
	</div>
);

export default BlogPagefn;



