//import _ from "lodash";
import React from "react";
import FaAnchor from "react-icons/lib/fa/anchor";
import FaBinoculars from "react-icons/lib/fa/binoculars";
import { compose, withProps, withState, withHandlers } from "recompose";
import { withScriptjs, withGoogleMap, GoogleMap, InfoWindow, Marker } from "react-google-maps";

const MarkerImage = () => (
  <FaBinoculars color='#FF0000' size='16'/>
);

const MapComponent = compose(
  withProps({
    googleMapURL:
      "https://maps.googleapis.com/maps/api/js?key=AIzaSyCUGkDjcIfZ9Z6mxKY3gwjOk9plCh6Fk4Q&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />
  }),
  withState('zoom', 'onZoomChange', 1),
  withHandlers(() => {
    const refs = {
      map: undefined,
    }

    return {
      onMapMounted: () => ref => {
        refs.map = ref
      },
      onZoomChanged: ({ onZoomChange }) => () => {
        onZoomChange(refs.map.getZoom())
      }
    }
  }),
  withScriptjs,
  withGoogleMap
)(props => (
  <GoogleMap defaultCenter={{ lat: 51.399366, lng: 0.016597 }}
    zoom={props.zoom} ref={props.onMapMounted} onZoomChanged={props.onZoomChanged}>
    <Marker position={{ lat: 51.399366, lng: 0.016597 }} >
      <InfoWindow onCloseClick={props.onToggleOpen}>
        <div>
          <FaAnchor />
          {" "}
          LONDON: {props.zoom}
        </div>
      </InfoWindow>
    </Marker>
    <Marker position={{ lat: 54.962878, lng: -1.647654 }} >
      <InfoWindow onCloseClick={props.onToggleOpen}>
        <div>
          <FaAnchor />
          {" "}
          NEWCASTLE
        </div>
      </InfoWindow>
    </Marker>
    <Marker position={{ lat: 22.569857, lng: 88.430194 }} >
      <InfoWindow onCloseClick={props.onToggleOpen}>
        <div>
          <FaAnchor />
          {" "}
          CALCUTTA
        </div>
      </InfoWindow>
    </Marker>
    <Marker position={{ lat: 12.933180, lng: 77.602524 }} >
      <InfoWindow onCloseClick={props.onToggleOpen}>
        <div>
          <FaAnchor />
          {" "}
          BANGALORE
        </div>
      </InfoWindow>
    </Marker>
    <Marker position={{ lat: 33.922093, lng: -84.336898 }} >
      <InfoWindow onCloseClick={props.onToggleOpen}>
        <div>
          <FaAnchor />
          ATLANTA
        </div>
      </InfoWindow>
    </Marker>
  </GoogleMap>
));

export default MapComponent;



