import React, { Component } from "react";
import ReactDisqusComments from "./_disqus";

class Disqus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toasts: []
    };
    this.notifyAboutComment = this.notifyAboutComment.bind(this);
    this.onSnackbarDismiss = this.onSnackbarDismiss.bind(this);
  }

  onSnackbarDismiss() {
    const [, ...toasts] = this.state.toasts;
    this.setState({ toasts });
  }
  notifyAboutComment() {
    const toasts = this.state.toasts.slice();
    toasts.push({ text: "New comment available!" });
    this.setState({ toasts });
  }
  render() {
    const { postNode } = this.props;
    
    //const post = postNode.frontmatter;
    //siteUrl + pathPrefix fomatting
    const url = 'https://sysforge-disqus-com.disqus.com' + '/sysforge-disqus-com';// + postNode.fields.slug;
    return (
      <ReactDisqusComments
        shortname='sysforge-disqus-com'
        identifier='sysforge-disqus-com'//{post.title}
        title='sysforge-disqus-com'//{post.title}
        url={url}
        category_id='123456'//{post.category_id}
        onNewComment={this.notifyAboutComment}
      />
    );
  }
}

export default Disqus;



